.works-page{ 
    width: 70%;
    
    .past-works{

        .work{
            margin: 40px auto;

            .banner{
                width: 100%;
            }

            .work-title{
                text-align: center;
            }

            .links{

                ul{
                    padding: 0;
    
                    li{
    
                        svg{
                            display: inline-block;
                            vertical-align: middle;
                        }
    
                        h3{
                            margin: 20px 5px;
                            font-size: 16px;
                            display: inline-block;
                            vertical-align: middle;
                        }
                    }
                }
            }
        }
    }
}